.press {
  padding-top: 87px;
  padding-bottom: 132px;
  background-color: var(--light-color);
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 33px;
    @include small-tablet {
      flex-wrap: wrap;
      gap: 20px;
    }
  }
  &__title {
    font-family: var(--second-family);
    font-size: 27px;
    font-weight: 400;
    line-height: 38px;
    letter-spacing: 1.2px;
    color: var(--dark-color);
  }
  &__link {
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0.02px;
    color: #00413e;
    border-radius: 80px;
    border: 1px solid rgba(53, 83, 98, 0.20);
    transition: color 0.3s ease-in-out;
    &:hover {
      transition: color 0.3s ease-in-out;
      // color: var(--light-color);
    }
  }
  &__list {
    row-gap: var(--offset);
  }
  &__item {
    grid-column: 4 span;
  }
}

.press-card {
  position: relative;
  border-top: 1px solid rgba(53, 83, 98, 0.3);
  padding-top: 16px;
  min-height: 88px;
  &__top {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 11px;
  }
  &__author {
    margin: -18px 0;
    padding: 18px 0;
    padding-left: 32px;
    color: var(--text-color);
    font-size: 11px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    background-position: left top 15px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
  }

  &__date {
    color: var(--text-color);
    font-size: 11px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
  }
  &__title {
    color: #00413e;
    font-size: 17px;
    font-weight: 400;
    line-height: 24.65px;
    transition: color 0.3s ease-in-out;
  }
  &__link {
    color: inherit;
    transition: color 0.3s ease-in-out;
    font-size: 17px;
    font-weight: 400;
    line-height: 24.65px;
    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }

  &__author--ted {
    background-image: url("./../img/approach-1.png");
  }
  &__author--news {
    background-image: url("./../img/press-2.png");
  }
  &__author--justice {
    background-image: url("./../img/press-3.png");
  }
  &__author--nyt {
    background-image: url("./../img/press-4.png");
  }
  &__author--corrections1 {
    background-image: url("./../img/press-5.png");
  }
  &__author--crime {
    background-image: url("./../img/press-6.png");
  }
}
