.info {
  position: relative;
  display: flex;
  height: 680px;
  background-color: #001414;

  &::before {
    content: "";
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url("./../img/info-1.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    @include tablet {
      display: none;
    }
  }
  &::after {
    content: "";
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url("./../img/info-2.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    @include tablet {
      display: none;
    }
  }

  &__content {
    // margin: 0 auto;
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
    border-right: 1px solid var(--dark-color);
    border-left: 1px solid var(--dark-color);
    padding: 0 82px;
    height: 100%;
    @include tablet {
      padding: var(--offset);
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 21px;
    color: var(--light-color);
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px;
  }
  &__descr {
    color: #fff;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
  }
  @include tablet {
    height: auto;
  }
}
