.footer {
  color: var(--light-color);
  &__top {
    padding-top: 112px;
    padding-bottom: 137px;
    background-color: #001414;
    .container {
      @include small-tablet {
        row-gap: var(--offset);
      }
    }
  }
  &__column {
    grid-column: 2 span;
    &:last-child {
      grid-column: 11 / 2 span;
      @include small-tablet {
        grid-column: 2 span;
      }
    }
  }

  &__title {
    position: relative;
    margin-bottom: 23px;
    padding-bottom: 24px;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #7d9798;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 1px;
      background-color: #25b895;
    }
  }
  &__link {
    font-size: 15px;
    font-weight: 400;
    line-height: 30px;
    color: #edeceb;
    transition: color 0.3s ease-in-out;
    &:focus {
      outline: none;
    }

    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }
  &__bottom {
    background-color: #000f0f;
    padding: 40px 0 63px;
    &-container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      @include small-tablet {
        flex-direction: column;
      }
    }
  }

  &__sublist {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    gap: 8px;
    @include small-tablet {
      margin-bottom: 20px;
      flex-direction: column;
    }
  }
  &__sublist-link {
    font-weight: 400;
    font-size: 9px;
    line-height: 10px;
    color: #e5ecec;
    transition: color 0.3s ease-in-out,;
    &:focus {
      outline: none;
    }
    @include small-tablet {
      font-size: 14px;
    }
    @include hover {
      color: var(--accent-color);
    }

    &:active {
      color: var(--active-color);
    }
  }
  &__bottom-content {
    display: flex;
    align-items: center;
  }
  &__logo {
    @include small-tablet {
      margin-bottom: 20px;
    }
  }
  &__copy {
    color: #e5ecec;
    font-size: 9px;
    font-weight: 400;
    line-height: 10px;
  }
  &__rights {
    font-size: 10px;
    font-weight: 400;
    line-height: 10px;
    color: #e5ecec;
  }
}
.footer__link.focus-visible {
  outline: 1px solid var(--active-color);
  outline-offset: 2px;
}
