.partners {
  padding: 16px 0;
  &__container {
    align-items: center;
    gap: 6px;
    @include big-desktop {
      justify-content: space-around;
    }
    @include tablet {
      flex-wrap: wrap;
      justify-content: flex-start;
    }
  }

  &__logo {
    @include big-desktop {
      max-width: 130px;
    }
  }
  &__title {
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--text-color);
  }
  &__list {
    display: flex;
    align-items: center;
    gap: 12px;

    @include big-desktop {
      justify-content: space-between;
    }
    @include tablet {
      flex-wrap: wrap;
      justify-content: flex-start;
    }
  }

  &__more {
    margin: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: var(--text-color);
  }
}
