.work {
  padding: 155px 0 133px;
  background-color: #001f1f;
  color: var(--light-color);
  &__content {
    grid-column: 4 span;
    @include small-tablet {
      margin-bottom: 20px;
    }
  }
  &__title {
    margin-bottom: 43px;
    font-family: var(--second-family);
    font-size: 36px;
    font-weight: 400;
    line-height: 52px;
    letter-spacing: 1.2px;
    color: #fafafa;
  }
  &__descr {
    margin-bottom: 37px;
    color: #fafafa;
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
  }
  &__btn {
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0.02px;
    background-color: #00665f;
    border: none;
  }
  &__map {
    grid-column: 7 / 5 span;
    @include small-tablet {
      grid-column: 4 span;
    }
  }
}
