.approuch {
  &__top {
    padding: 80px 0;
    background-color: #f7f8f8;
  }

  &__left {
    padding-top: 33px;
    grid-column: 5 span;

    @include big-desktop {
      margin-bottom: var(--offset);
    }
    @include small-tablet {
      grid-column: 4 span;
    }
  }

  &__title {
    margin-bottom: 13px;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__descr {
    margin-bottom: 37px;
    font-size: 15px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    color: var(--text-color);
  }

  &__btn {
    color: #00413E;
    border-color: rgba(53, 83, 98, 0.2);
    padding: 16px 34px;
  }

  &__right {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    grid-column: 7 / 6 span;
    gap: 8px;

    @include big-desktop {
      grid-column: 12 span;
      justify-content: flex-start;
    }
    @include small-tablet {
      grid-column: 4 span;
      flex-wrap: wrap;
    }
  }

  &__bottom {
    padding: 24px 0;
    background-color: var(--light-color);
  }


  &__item {
    grid-column: 2 span;
  }

  &__link {
    display: block;
    border-top: 1px solid rgba(53, 83, 98, 0.30);
    padding-top: 17px;
    padding-left: 32px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--text-color);
    background-position: left top 15px;
    background-size: 24px 24px;
    background-repeat: no-repeat;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: var(--accent-color);
      color: var(--accent-color);
    }

    &:active {
      border-color: var(--active-color);
      color: var(--active-color);
    }

  }
    &__link--ted {
      background-image: url("./../img/approach-1.png");
    }

    &__link--nyt {
      background-image: url("./../img/approach-2.png");
    }

    &__link--scoop {
      background-image: url("./../img/approach-3.png");
    }

    &__link--press {
      background-image: url("./../img/approach-4.png");
    }

    &__link--time {
      background-image: url("./../img/approach-5.png");
    }

    &__link--freethink {
      background-image: url("./../img/approach-6.png");
    }

}
.approuch__link.focus-visible {
  border-color: var(--active-color);
}
